body {
  font-family: 'Poppins', sans-serif; }

.external {
  position: relative;
  overflow: hidden;
  min-height: 800px; }
  .external * {
    transition: none; }
  .external .hero-area {
    width: 60%;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  .external .textarea {
    margin-top: -40px;
    text-align: right;
    width: 100%;
    position: absolute; }
    .external .textarea button {
      float: right; }
    .external .textarea .staggerP {
      padding-top: 40px;
      max-width: 300px;
      right: -10px;
      opacity: 0;
      position: absolute; }
      .external .textarea .staggerP p {
        font-weight: 300;
        display: block;
        text-align: left;
        position: relative;
        line-height: 1.4em; }
  .external h1 {
    font-size: 50px; }
  .external g path {
    mix-blend-mode: screen; }
  .external .bookmark {
    -webkit-writing-mode: vertical-rl;
            writing-mode: vertical-rl;
    color: white;
    position: fixed;
    left: 40px;
    bottom: 50px;
    display: inline; }
    .external .bookmark span {
      opacity: 0.3;
      padding-top: 10px; }
    .external .bookmark svg {
      float: left; }
  .external #triangle, .external #square {
    opacity: 0; }
  .external #g1, .external #g2, .external #g3, .external #g4 {
    visibility: hidden; }
    .external #g1 path, .external #g2 path, .external #g3 path, .external #g4 path {
      stroke-linecap: round;
      stroke-width: 9; }
  .external .iconBrand {
    position: fixed;
    top: 10px;
    left: 20px;
    width: 100px;
    opacity: 0.3; }
  .external .watermark {
    position: absolute;
    bottom: 40px;
    right: 30px;
    width: 300px;
    opacity: 0.15; }
  .external .search {
    position: fixed;
    top: 40px;
    right: 40px;
    display: inline-block;
    vertical-align: middle; }
    .external .search .searchtext {
      opacity: 0.3;
      vertical-align: top; }
    .external .search .searchicon {
      margin-left: 10px;
      width: 17px; }
  .external .button {
    display: block;
    padding: 10px 30px;
    border: none;
    width: 170px;
    background: none;
    color: inherit;
    vertical-align: middle;
    position: relative;
    z-index: 1;
    border: 3px solid white;
    font-family: 'Poppins', sans-serif;
    -webkit-backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    margin-top: -15px;
    overflow: hidden;
    transition: border-color 0.3s, color 0.3s;
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1); }
  .external .button:focus {
    outline: none; }
  .external .button > span {
    vertical-align: top; }
    .external .button > span svg {
      vertical-align: top; }
  .external .button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 150%;
    height: 100%;
    background: #37474f;
    z-index: -1;
    -webkit-transform: rotate3d(0, 0, 1, -45deg) translate3d(0, -3em, 0);
    transform: rotate3d(0, 0, 1, -45deg) translate3d(0, -3em, 0);
    -webkit-transform-origin: 0% 100%;
    transform-origin: 0% 100%;
    transition: opacity 0.3s, background-color 0.3s, -webkit-transform 0.3s;
    transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
    transition: transform 0.3s, opacity 0.3s, background-color 0.3s, -webkit-transform 0.3s; }
  .external .button:hover {
    color: black;
    border-color: white;
    cursor: pointer; }
  .external .button:hover::before {
    opacity: 1;
    background-color: white;
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
    transform: rotate3d(0, 0, 1, 0deg);
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1); }
  @media screen and (max-width: 600px) {
    .external h1 {
      font-size: 32px;
      margin-top: 30px; }
    .external button, .external p {
      font-size: 14px; }
    .external .iconBrand {
      width: 50px; }
    .external .search {
      top: 20px;
      right: 20px;
      opacity: 0.3; }
      .external .search .searchtext {
        display: none; }
    .external .bookmark {
      left: 20px; } }
  .external .share, .external .home {
    display: none;
    opacity: 0; }

* {
  transition: all 0.4s linear;
  font-family: 'Ubuntu', sans-serif; }

.slide-up {
  -webkit-animation: slide-up 0.4s ease;
          animation: slide-up 0.4s ease; }

@-webkit-keyframes slide-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes slide-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

